<template>
  <div id="all">
    <van-overlay :show="vanshow" z-index="10000">
      <van-loading type="spinner" class="loding"
    /></van-overlay>
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      @click-left="uppage"
      left-arrow
      fixed
      placeholder
    >
      <template #right>
        <van-icon name="share-o" @click="showShare = true" size="18" />
      </template>
    </van-nav-bar>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
    />
    <div v-if="all">
      <!-- swiper -->
      <SwiperCom :all="all" />
      <!-- product -->
      <InfoCom :all="all" />
      <div class="goods-info">
        <!-- 关键参数 -->
        <ParametersCom :all="all" />
        <!-- 活动 -->
        <div class="activity">
          <span>活动</span>
          <span>超值加购</span>
          <span
            >加299元获得各种各样的奶茶小吃，欢迎光临，红豆奶茶好喝，你喜欢喝吗</span
          >
        </div>
        <!-- 信息 -->
        <div class="info-info">
          <!-- 具体信息 -->

          <div class="info1" v-if="all && all.goods_info && all.goods_info[0]">
            <span>已选</span>
            <span>
              <van-cell
                is-link
                @click="showPopup3"
                class="van-cell"
                v-if="all.goods_info && all.goods_info.length"
              >
                <div>
                  <i>{{ all.goods_info[0].name }} x1</i>
                </div>
                <div class="optional">可选增值服务</div>

                <div class="ovoimg">
                  <van-image
                    class="img"
                    v-for="(o, i) in all.goods_info.slice(0, 3)"
                    :key="i"
                    :src="o.image_share"
                    alt=""
                  >
                    <template v-slot:error>加载失败</template>
                  </van-image>
                  <span>
                    <i>点击查看</i>
                  </span>
                </div>
              </van-cell>
              <van-popup
                v-model="showinfo1"
                position="bottom"
                :style="{ height: '70%' }"
              >
                <main class="over">
                  <div class="show">
                    <template v-for="show in all.goods_info">
                      <template
                        v-if="
                          show.prop_list.some(
                            (obj) =>
                              obj.prop_cfg_id === vid.prop_cfg_id &&
                              obj.prop_value_id === vid.prop_value_id
                          ) &&
                          show.prop_list.some(
                            (obj) =>
                              obj.prop_cfg_id === cid.prop_cfg_id &&
                              obj.prop_value_id === cid.prop_value_id
                          )
                        "
                      >
                        <div class="show-item" :key="show.goods_id">
                          <div class="show-img">
                            <van-image
                              class="img"
                              lazy-load
                              :src="show.img_url"
                              alt=""
                            >
                              <template v-slot:error>加载失败</template>
                            </van-image>
                          </div>
                          <div class="show-info">
                            <span class="price">{{ show.price }}</span>
                            <span class="name">{{ show.name }}</span>
                          </div>
                        </div>
                      </template>
                    </template>
                  </div>
                  <div class="baby"></div>

                  <i>
                    <van-cell
                      is-link
                      @click="showPopup"
                      color="#ccc"
                      class="address"
                      ><van-icon name="guide-o" />广东省广州市从化区</van-cell
                    >
                    <van-popup
                      v-model="show"
                      position="bottom"
                      :style="{ height: '50%' }"
                    >
                      <van-area title="选择收货地址" :area-list="areaList" />
                    </van-popup>
                  </i>
                  <div class="ovo-info">
                    <template v-for="optin in all.buy_option">
                      <h1 class="option" :key="optin.prop_cfg_id">
                        <div class="title">{{ optin.name }}</div>
                        <div class="item-list">
                          <template v-for="item in optin.list">
                            <div
                              class="item"
                              :class="{
                                active:
                                  isActiveC === item.prop_value_id ||
                                  isActiveV === item.prop_value_id,
                              }"
                              :key="item.prop_value_id"
                              @click="
                                getoptionid(
                                  optin.prop_cfg_id,
                                  item.prop_value_id
                                )
                              "
                            >
                              {{ item.name }}
                            </div>
                          </template>
                        </div>
                      </h1>
                    </template>
                    <div class="buy-num">
                      <span>购买数量</span>
                      <van-stepper v-model="value" />
                    </div>
                  </div>
                </main>
                <van-goods-action class="bt">
                  <van-goods-action-button
                    type="warning"
                    @click="addcars"
                    text="加入购物车"
                  />
                  <van-goods-action-button
                    type="danger"
                    @click="showPopup3"
                    text="立即购买"
                  />
                </van-goods-action>
              </van-popup>
            </span>
          </div>

          <div class="info2">
            <span style="margin: 10px">地址</span>

            <span>
              <van-cell is-link @click="showPopup" color="#ccc" class="van-cell"
                >配送地址</van-cell
              >
              <van-popup
                v-model="show"
                position="bottom"
                :style="{ height: '50%' }"
              >
                <van-area title="选择收货地址" :area-list="areaList" />
              </van-popup>
            </span>
          </div>
          <!-- 服务 -->
          <ServeCom :all="all" />
        </div>
        <!-- 评论 -->
        <div class="comments" v-if="all.goods_share_datas.comments">
          <div class="comments-title">
            <span>买家秀</span>
            <span
              >共{{
                formatNumber(
                  all.goods_share_datas.comments.detail.comments_total
                )
              }}条评论&gt;</span
            >
          </div>
          <div class="comments-tags">
            <span
              v-for="(
                item, index
              ) in all.goods_share_datas.comments.detail.comment_tags.slice(
                0,
                4
              )"
              :key="index"
              >{{ item.tag_name }}</span
            >
          </div>
          <div class="comments-img">
            <div
              v-for="(item, index) in all.goods_share_datas.comments.list.slice(
                0,
                4
              )"
              :key="index"
            >
              <van-image
                class="img"
                v-for="(o, i) in item.comment_images.slice(0, 1)"
                :key="i"
                :src="o"
                alt=""
              >
                <template v-slot:error>加载失败</template>
              </van-image>
            </div>
          </div>
        </div>
      </div>
      <!-- 商品详细信息 -->
      <template>
        <div class="tab-container">
          <div class="tab-header" ref="stickyHeader">
            <div
              class="tab-item"
              v-for="(tab, index) in tabs"
              :key="index"
              :class="{ active: activeTab === index }"
              @click="switchTab(index)"
            >
              {{ tab.name }}
            </div>
          </div>

          <div class="tab-content" style="line-height: 0">
            <div v-show="activeTab === 0">
              <div v-for="(a, b) in all.goods_tpl_datas" :key="b">
                <div v-for="(c, d) in a.sections" :key="d">
                  <template v-if="c.view_type === 'product_info_tab'">
                    <template v-for="e in c.body.items">
                      <template v-if="e.tab_name === '商品介绍'">
                        <van-image
                          class="img"
                          lazy-load
                          style="width: 100vw"
                          v-for="(g, h) in e.page_info"
                          :key="h"
                          :src="g.body.img_url"
                          alt=""
                        >
                          <template v-slot:error>加载失败</template>
                        </van-image>
                      </template>
                    </template>
                  </template>
                </div>
              </div>
            </div>
            <div v-show="activeTab === 1">
              <div v-for="(a, b) in all.goods_tpl_datas" :key="b">
                <div v-for="(c, d) in a.sections" :key="d">
                  <template v-if="c.view_type === 'product_info_tab'">
                    <template v-for="e in c.body.items">
                      <template v-if="e.tab_name === '规格参数'">
                        <van-image
                          lazy-load
                          style="width: 100vw"
                          v-for="(g, h) in e.page_info"
                          :key="h"
                          :src="g.body.img_url"
                          alt=""
                        >
                          <template v-slot:error>加载失败</template>
                        </van-image>
                      </template>
                    </template>
                  </template>
                </div>
              </div>
            </div>
          </div>
        </div>
      </template>
    </div>
    <div v-else>
      <van-empty description="暂无商品" />
    </div>
    <RecomShop />

    <van-goods-action style="z-index: 11">
      <van-goods-action-icon icon="chat-o" text="客服" />
      <van-goods-action-icon icon="cart-o" @click="jumpcar" text="购物车" />
      <van-goods-action-icon icon="wap-home-o" @click="jumphome" text="首页" />
      <van-goods-action-button
        type="warning"
        @click="showPopup3"
        text="加入购物车"
      />
      <van-goods-action-button
        type="danger"
        @click="showPopup3"
        text="立即购买"
      />
    </van-goods-action>
  </div>
</template>

<script>
import axios from "axios";
// import _ from "lodash";
import { areaList } from "@vant/area-data";
import RecomShop from "@/components/RecomShop.vue";
import { Notify } from "vant";
import SwiperCom from "../components/shopdetail/SwiperCom.vue";
import InfoCom from "../components/shopdetail/InfoCom.vue";
import ParametersCom from "../components/shopdetail/ParametersCom.vue";
import ServeCom from "../components/shopdetail/ServeCom.vue";

export default {
  components: {
    RecomShop,
    SwiperCom,
    InfoCom,
    ParametersCom,
    ServeCom,
  },
  data() {
    return {
      all: {
        goods_share_datas: {
          comments: {
            detail: {
              comments_total: 0,
              comment_tags: [],
            },
            list: [],
          },
        },
        buy_option: [
          {
            name: "版本",
            list: [
              { prop_value_id: 1, name: "版本1" },
              { prop_value_id: 2, name: "版本2" },
              { prop_value_id: 3, name: "版本3" },
            ],
          },
          {
            name: "颜色",
            list: [
              { prop_value_id: 1, name: "红色" },
              { prop_value_id: 2, name: "绿色" },
              { prop_value_id: 3, name: "蓝色" },
            ],
          },
        ],
      },
      vid: { prop_cfg_id: 0, prop_value_id: 0 },
      cid: { prop_cfg_id: 0, prop_value_id: 0 },
      isActiveC: null,
      isActiveV: null,

      versionmoren: null,
      colormoren: null,
      current: 0,
      carsitem: {},
      areaList,
      show: false,
      vanshow: true,
      value: 1,
      showinfo1: false,
      ovovalue: 1,
      tabs: [{ name: "商品介绍" }, { name: "规格参数" }],
      activeTab: 0, // 当前激活的选项卡索引

      showShare: false,
      options: [
        [
          { name: "微信", icon: "wechat" },
          { name: "朋友圈", icon: "wechat-moments" },
          { name: "微博", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
    };
  },

  methods: {
    showPopup3() {
      // if (!this.username || this.username.trim() === "") {
      //   Notify({ type: "warning", message: "请先登录！" });
      //   // 确保正确传递路径
      //   this.$router.push({
      //     path: "/login",
      //     query: {
      //       target: this.$route.fullPath, // 当前页面的路径
      //     },
      //   });
      //   return;
      // }
      this.showinfo1 = true;
    },
    addcars() {
      console.log("加入购物车点击，carsid:", this.filteredGoodsInfo);
      console.log(this.username);

      // 调用 Vuex 的 action
      const newProduct = {
        id: this.filteredGoodsInfo[0].product_id,
        img: this.filteredGoodsInfo[0].img_url,
        name: this.filteredGoodsInfo[0].name,
        price: this.filteredGoodsInfo[0].price,
        quantity: this.value,
        selected: false,
      };
      this.$store.dispatch("addProduct", {
        username: this.username,
        newProduct,
      });
      Notify({ type: "success", message: "商品已添加" });
    },
    uppage() {
      this.$router.go(-1);
    },
    jumpcar() {
      this.$router.push("/car");
    },
    jumphome() {
      this.$router.push("/");
    },
    getoptionid(bigID, smallID) {
      console.log(bigID, smallID);

      if (bigID == this.vid.prop_cfg_id) {
        this.vid.prop_value_id = smallID;
        this.isActiveV = smallID;
      } else {
        this.cid.prop_value_id = smallID;
        this.isActiveC = smallID;
      }

      console.log("this.pp", this.vid, this.cid);
    },

    close() {
      this.showinfo1 = false;
    },
    switchTab(index) {
      this.activeTab = index; // 切换到对应的选项卡
    },
    formatNumber(num) {
      if (num >= 10000) {
        return `${Math.floor(num / 10000)}w+`;
      }
      return num;
    },

    getDetail(id) {
      axios
        .get(`https://apis.netstart.cn/xmsc/miproduct/view`, {
          params: {
            commodity_id: id,
          },
        })
        .then((response) => {
          console.log("商品详情:", response.data.data);
          this.all = response.data.data;
          console.log(this.prop_list);
          this.vid.prop_cfg_id = this.all?.buy_option[0].prop_cfg_id;
          this.cid.prop_cfg_id = this.all?.buy_option[1].prop_cfg_id;

          this.vid.prop_value_id = this.all.buy_option[0].list[0].prop_value_id;
          this.cid.prop_value_id = this.all.buy_option[1].list[0].prop_value_id;

          this.isActiveV = this.all.buy_option[0].list[0].prop_value_id;
          this.isActiveC = this.all.buy_option[1].list[0].prop_value_id;
        })
        .catch((error) => {
          console.error("获取商品详情失败:", error);
        });
    },

    showPopup() {
      this.show = true;
    },
  },
  computed: {
    // 获取本地存储中的 token
    token() {
      return localStorage.getItem("token");
    },
    // 从 token 获取 username
    username() {
      return this.token ? JSON.parse(this.token) : "";
    },
    filteredGoodsInfo() {
      return this.all.goods_info.filter((a) => {
        return (
          a.prop_list.some(
            (obj) =>
              obj.prop_cfg_id === this.vid.prop_cfg_id &&
              obj.prop_value_id === this.vid.prop_value_id
          ) &&
          a.prop_list.some(
            (obj) =>
              obj.prop_cfg_id === this.cid.prop_cfg_id &&
              obj.prop_value_id === this.cid.prop_value_id
          )
        );
      });
    },
  },
  created() {
    const currentId = this.$route.query.id;
    this.getDetail(currentId);
    setTimeout(() => {
      this.vanshow = false;

    }, 3000);
  },
  watch: {},
};
</script>

<style lang="less" scoped>
#all {
  background-color: #c0bdbd27;
  .loding {
    top: 45%;
    left: 45%;
  }
}

.goods-info {
  .activity {
    margin: 10px;
    padding: 10px;
    white-space: nowrap;
    font-size: 14px;
    overflow: hidden;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 8px;
    span:first-child {
      color: #88888880;
    }

    span:nth-child(2) {
      color: #ff0000;
      font-weight: bold;
      margin: 0 10px;
      border: 1px solid #ff0000;
    }
  }

  .info-info {
    border: 1px solid #ccc;
    margin: 10px;
    border-radius: 8px;
    .info1 {
      display: flex;
      justify-content: space-between;
      background-color: white;
      border-radius: 8px 8px 0 0;
      .over {
        margin-bottom: 50px;
        position: relative;
      }
      > span:first-child {
        margin: 10px;
      }
      > span:last-child {
        border-radius: 0 8px 0 0;
        width: 80%;
        .van-cell {
          font-size: 14px;
          border-radius: 0 8px 0 0;
          padding: 0;
          > div {
            margin-bottom: 10px;
          }
          .optional {
            color: red;
            margin: 0 10px;
          }
          .ovoimg {
            display: flex;

            .img {
              width: 40px;
              height: 40px;
              margin-right: 10px;
              background: #f8f8f8;
              border-radius: 8px;
            }

            > span {
              width: 40px;
              height: 40px;
              display: flex;
              flex-wrap: wrap;
              justify-content: center;
              align-items: center;
              background: #cacaca39;
              border-radius: 8px;

              i {
                font-size: 10px;
                width: 20px;
                line-height: 12px;
              }
            }
          }
        }
      }
      .ovo-info {
        padding: 10px;
        .option {
          .title {
            font-size: 18px;
            font-weight: 700;
            margin: 10px 0;
          }
          .item-list {
            display: flex;
            flex-wrap: wrap;
            .item {
              padding: 10px;
              border-radius: 30px;
              background-color: #f5f5f5;
              font-size: 14px;
              margin: 10px 0 0 10px;
            }
            .active {
              background-color: rgba(255, 0, 0, 0.266);
              color: rgb(255, 0, 0);
              border-color: #ff0000;
            }
          }
        }
        .buy-num {
          margin: 20px 0;
          display: flex;
          justify-content: space-between;
        }
      }
      .baby {
        height: 108px;
      }
      .show {
        padding: 10px;
        position: absolute;
        top: 0;
        left: 0;

        .show-item {
          display: flex;
          width: 100%;
          .show-img {
            margin-right: 20px;
            background-color: #ededed;
            border-radius: 20px;
            .img {
              width: 85px;
              border-radius: 20px;
            }
          }

          .show-info {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            font-weight: 600;
            padding: 10px 0;
            .price {
              font-size: 18px;
              color: #ff1f05;
              &::before {
                content: "¥";
              }
            }
            .name {
              font-size: 16px;
            }
          }
        }
      }
      i {
        margin: 10px;
        border-radius: 8px;
        background-color: #f8f8f8;
        .address {
        }
      }
    }
    > .info2 {
      display: flex;
      justify-content: space-between;
      background-color: white;
      line-height: 24px;
      span:last-child {
        padding: 10px;
        display: flex;
        align-items: center;
        .van-cell {
          width: 100%;
          display: flex;
          padding: 0;
          justify-content: space-between;
        }
      }
    }
  }

  .comments {
    padding: 10px;
    background-color: white;
    border: 1px solid #ccc;
    margin: 10px;
    border-radius: 8px;
    .comments-title {
      font-size: 14px;
      display: flex;
      justify-content: space-between;

      span:last-child {
        color: #88888880;
      }
    }

    .comments-tags {
      display: flex;
      font-size: 12px;

      span {
        margin: 10px 10px 10px 0px;
        background: #fff2eb;
        padding: 5px;
        border-radius: 8px;
      }
    }

    .comments-img {
      display: flex;
      justify-content: space-around;

      > div {
        .img {
          width: 78rem;
          height: 78rem;
          object-fit: cover;
        }
      }
    }
  }
}

.tab-container {
  width: 100%;
  margin: 0 auto;
  font-family: Arial, sans-serif;

  .tab-header {
    display: flex;
    position: sticky;
    top: 45px;
    /* 距离页面顶部的距离 */
    background-color: #fff;
    z-index: 10;
    border-bottom: 1px solid #ddd;

    .tab-item {
      flex: 1;
      text-align: center;
      padding: 10px 0;
      cursor: pointer;
      transition: background-color 0.3s, color 0.3s;

      &:hover {
        background-color: #f5f5f5;
      }

      &.active {
        font-weight: bold;
        color: red;
      }
    }
  }

  .tab-content {
    p {
      font-size: 16px;
      line-height: 1.5;
      color: #333;
    }
  }
}
</style>
