<template>
  <div style="background-color: #ccc">
    <div class="returrn">
      <div class="son" style="background-color: white">
        <van-icon name="arrow-left" @click="$router.go(-1)" class="edit" />
      </div>
    </div>

    <main >
      <h2>
        <template v-for="list in order">
          <div :key="list.id" class="order" @click="jumppay(list)" v-if="!list.ispay">
            <i class="title"><span>官方自营</span> <span>去付款</span></i>
            <van-divider />
            <template v-for="(item, index) in list.selectedItems">
              <div :key="index" class="item-boos">
                <div class="item">
                  <span class="item-img">
                    <van-image class="img" :src="item.img" alt="">
                      <template v-slot:error>加载失败</template>
                    </van-image></span
                  >
                  <span class="info"
                    ><b class="name">{{ item.name }}</b>
                    <b class="quantity">数量:{{ item.quantity }}</b></span
                  >
                </div>
                <div class="price">
                  <div>
                    <span>订单编号:</span> <span>{{ list.id }}</span>
                  </div>
                  <div>
                    <span>单价:</span> <span>￥{{ item.price }}</span>
                  </div>
                  <div>
                    <span>总价格：</span
                    ><span>￥{{ item.price * item.quantity }}</span>
                  </div>
                  <div>
                    <span>剩余时间：</span><span>{{ item.date }}</span>
                  </div>
                </div>
              </div>
            </template>
          </div>
        </template>
      </h2>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  computed: {
    // 获取 vuex 中的 userlist 数据
    userlist() {
      return this.$store.state.userlist;
    },
    // 获取本地存储中的 token
    token() {
      return localStorage.getItem("token");
    },
    // 从 token 获取 username
    username() {
      return this.token ? JSON.parse(this.token) : "";
    },
    // 通过用户名获取当前登录用户的 selectedItemsList
    order() {
      const user = this.userlist.find(
        (user) => user.username === this.username
      );
      return user ? user.order : []; // 如果找到对应用户，返回其 selectedItemsList，否则返回空数组
    },
  },
  methods: {
    jumppay(list) {
      this.$router.push({
        path: "/paydetail",
        query: { list: JSON.stringify(list) }, // 传递list参数
      });
    },
  },
};
</script>

<style lang="less" scoped>
main {
  padding: 10px;
  h1 {
    border: 1px solid #ccc;
    border-radius: 12px;
    .van-cell {
      border-radius: 12px;
    }
  }
  h2 {
    .order {
      padding: 20px 10px;
      box-sizing: border-box;
      background-color: white;
      border-radius: 12px;
      padding: 10px 0;
      margin: 10px 0;

      .title {
        margin: 0 10px;
        display: block;
        display: flex;
        justify-content: space-between;
      }
      .item-boos {
        .item {
          display: flex;
          .item-img {
            .img {
              width: 100px;
            }
          }
          .info {
            margin-left: 20px;
            padding-top: 20px;
            padding-right: 10px;
            .name {
              font-size: 16px;
              font-weight: 600;
              line-height: 20px;
            }
            .quantity {
              margin-top: 30px;
              font-size: 14px;
              color: #999;
              display: block;
              text-align: right;
            }
          }
        }
        .price {
          padding: 0 10px;
          margin: 10px 0;
          font-size: 14px;
          > div {
            margin: 10px 0;
            display: flex;
            justify-content: space-between;
          }
        }
      }
    }
  }
}
</style>