<template>
  <div style="margin-bottom: 60rem">
    <header>
      <div class="content" @click="jumplogin" v-if="!ovostore">
        <div class="header-img">
          <span class="iconfont icon-wode"></span>
        </div>
        <div>登录/注册</div>
      </div>
      <div class="content" v-else>
        <div class="header-img">
          <span class="iconfont icon-wode"></span>
        </div>
        <div>{{ ovostore }}</div>
      </div>
    </header>
    <main>
      <div class="order">
        <div class="content">
          <div>我的订单</div>
          <div>全部订单<van-icon name="arrow" /></div>
        </div>
      </div>
      <div class="van-hairline--top"></div>

      <div class="item_3">
        <div class="content">
          <div class="item" @click="gotogoodspay">
            <div class="iconfont icon-dingdandaifukuan"></div>
            <div>待付款</div>
          </div>
          <div class="item" @click="gotoPayView">
            <div class="iconfont icon-31daishouhuo"></div>
            <div>待收货</div>
          </div>
          <div class="item">
            <div class="iconfont icon-tuihuanxiu"></div>
            <div>退还修</div>
          </div>
        </div>
      </div>
      <div class="item_2">
        <div class="content">
          <div class="item">
            <span style="color: #fac600" class="iconfont icon-huiyuan"></span>
            <div>会员中心</div>
            <van-icon class="ovoicon" name="arrow" />
          </div>
          <hr />
          <div class="item">
            <span
              style="color: #53bbe2"
              class="iconfont icon-wodeyouhui"
            ></span>
            <div>我的优惠</div>
            <van-icon class="ovoicon" name="arrow" />
          </div>
        </div>
      </div>
      <div class="item_2">
        <div class="content">
          <div class="item">
            <span
              style="color: #fe4756"
              class="iconfont icon-fuwuzhongxin"
            ></span>
            <div>服务中心</div>
            <van-icon class="ovoicon" name="arrow" />
          </div>
          <hr />
          <div class="item">
            <span style="color: #7700a9" class="iconfont icon-shouye"></span>
            <div>茄子之家</div>
            <van-icon class="ovoicon" name="arrow" />
          </div>
        </div>
      </div>
      <div class="item_2">
        <div class="content">
          <div class="item">
            <span style="color: #fac600" class="iconfont icon-icon"></span>
            <div>我的F码</div>
            <van-icon class="ovoicon" name="arrow" />
          </div>
          <hr />
          <div class="item">
            <span style="color: #53bbe2" class="iconfont icon-liwu"></span>
            <div>礼物兑换码</div>
            <van-icon class="ovoicon" name="arrow" />
          </div>
        </div>
      </div>
      <div
        class="set"
        @click="
          $router.push({
            path: '/my/set',
          })
        "
      >
        <div class="content">
          <span class="iconfont icon-shezhi"></span>
          <div>设置</div>
          <van-icon class="ovoicon" name="arrow" />
        </div>
      </div>
    </main>
  </div>
</template>

<script>
// import {getfenlei} from '@/api/index'
import store from "@/store";
import { Notify } from "vant";

export default {
  data() {
    return {
      ovostore: store.state.token,
    };
  },
  computed: {
    // 获取 vuex 中的 userlist 数据
    userlist() {
      return this.$store.state.userlist;
    },
    // 获取本地存储中的 token
    token() {
      return localStorage.getItem("token");
    },
    // 从 token 获取 username
    username() {
      return this.token ? JSON.parse(this.token) : "";
    },
  },
  methods: {
    jumplogin() {
      this.$router.push({
        path: "/login",
        query: {
          target: this.$route.fullPath, // 当前页面的路径
        },
      });
    },
    gotogoodspay(){
      if (!this.username || this.username.trim() === "") {
        Notify({ type: "warning", message: "请先登录！" });
        // 确保正确传递路径
        this.$router.push({
          path: "/login",
          query: {
            target: this.$route.fullPath, // 当前页面的路径
          },
        });
        return;
      }
      this.$router.push({
        path: '/goodspay',
      })
    },
    gotoPayView(){
      if (!this.username || this.username.trim() === "") {
        Notify({ type: "warning", message: "请先登录！" });
        // 确保正确传递路径
        this.$router.push({
          path: "/login",
          query: {
            target: this.$route.fullPath, // 当前页面的路径
          },
        });
        return;
      }
      this.$router.push({
        path: '/pay',
      })
    }
  },
  created() {},
};
</script>

<style lang="less" scoped>
header {
  background: url("@/assets/bg.png") center 0 #6a4ba7;
  background-size: auto 100%;
  padding: 32rem 0;
  .content {
    display: flex;
    align-items: center;
    color: white;
    background-color: #dcdcdc6d;
    margin: 0 20px;
    padding: 10px 0;

    .header-img {
      margin: 0 20px;
      background-color: #debaf46d;
      padding: 10px;
      border-radius: 50%;
      .iconfont {
        font-size: 30px;
      }
    }
  }
}
main {
  background-color: #f5f5f5;

  .content {
    background-color: #fff;
    padding: 10px;
  }
  .order {
    .content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: #fff;
    }
  }
  .item_3 {
    .content {
      display: flex;
      justify-content: space-around;
      align-items: center;
      background-color: #fff;
      text-align: center;
      .item {
        .iconfont {
          font-size: 30px;
          margin: 10px;
          color: #888;
        }
      }
    }
  }
  .item_2 {
    margin-top: 20px;
    .content {
      .item {
        display: flex;
        height: 55rem;
        align-items: center;
        position: relative;
        span {
          font-size: 35rem;
          margin-right: 30rem;
        }
        .ovoicon {
          position: absolute;
          top: 20rem;
          right: 10rem;
        }
      }
    }
  }
  .set {
    margin-top: 20px;
    .content {
      display: flex;
      height: 55rem;
      align-items: center;
      position: relative;
      span {
        font-size: 35rem;
        margin-right: 30rem;
      }
      .ovoicon {
        position: absolute;
        top: 20rem;
        right: 10rem;
      }
    }
  }
}
</style>