    <QuestionCom />
  </div>
</template>

<script>
import axios from "axios";
import QuestionCom from "@/components/round/QuestionCom.vue";
export default {
  components: {
    QuestionCom,
  },
  data() {
    return {
      // 模拟图片路径，可以动态替换
    };
  },
  methods: {
    getcontent() {
      axios
        .get(`https://apis.netstart.cn/xmsc/communicate/home_rec?size=10`)
        .then((response) => {
          console.log("评论:", response.data.data);
        })
        .catch((error) => {
          console.error("获取评论失败:", error);
        });
    },
  },
  created() {
    this.getcontent();
  },
};
</script>

