<script>
import {
  getxmIndex,
  getxmSmart,
  getxmTv,
  getxmAppliance,
  getxmNote,
} from "@/api/index";
// 导入首页 推荐组件
import SwipeCom from "@/components/index/SwiperCom.vue";
import DividerLine from "@/components/index/DividerLine.vue";
import ListTwoType13 from "@/components/index/ListTwoType13.vue";
import ListActionTitle from "@/components/index/ListActionTitle.vue";
import CellsAutoFill from "@/components/index/CellsAutoFill.vue";
import ListThreeType4 from "@/components/index/ListThreeType4.vue";
import ListTwoType14 from "@/components/index/ListTwoType14.vue";
import ListOneType14 from "@/components/index/ListOneType14.vue";
import SearchLogo from "@/components/index/SearchLogo.vue";
// import { set } from "core-js/core/dict";

export default {
  name: "HomeView",
  components: {
    SwipeCom,
    DividerLine,
    ListTwoType13,
    ListActionTitle,
    CellsAutoFill,
    ListThreeType4,
    ListTwoType14,
    ListOneType14,
    SearchLogo,
  },
  data: function () {
    return {
      indexlist: [],
      smart: [],
      tv: [],
      appliance: [],
      note: [],
      active: 0,
      show: true,
    };
  },
  created() {
    getxmIndex().then((response) => {
      console.log("indexlist", response.data.data.data.sections);

      this.indexlist = response.data.data.data.sections;
    });

    getxmSmart().then((response) => {
      this.smart = response.data.data.data.sections;
    });
    getxmTv().then((response) => {
      this.tv = response.data.data.data.sections;
    });
    getxmAppliance().then((response) => {
      console.log("appliance", response.data.data.data.sections);
      this.appliance = response.data.data.data.sections;
    });
    getxmNote().then((response) => {
      console.log("note", response.data.data.data.sections);
      this.note = response.data.data.data.sections;
    });
    setTimeout(() => {
      this.show = false;
    }, 3000);
  },
};
</script>
