<template>
  <div>
    <!-- <van-overlay :show="show" z-index="10000">
      <van-loading type="spinner" size="200rem" class="loding"
    /></van-overlay> -->
    <div class="abc">
      <div class="son">
        <van-icon name="arrow-left" @click="$router.go(-1)" class="edit" />
        <div>买家秀详情</div>
        <van-icon name="ellipsis" />
      </div>
    </div>
    <template v-if="data">
      <div class="top">
        <b>
          <van-image
            style="width: 32px"
            lazy-load
            :src="data?.entity_circle.img"
