<script>
import axios from "axios";

export default {
  data() {
    return {
      show: false, // 模拟图片路径，可以动态替换
      data: [],
    };
  },
  created() {
    setTimeout(() => {
      // this.show=false
    }, 1000);
    this.getcontent();
  },
  methods: {
    getcontent() {
      axios
        .get(
          `https://apis.netstart.cn/xmsc/communicate/mizone_product_comment_detail?comment_id=215265631`
        )
        .then((response) => {
          this.data = response.data.data;
          console.log("评论:", response.data.data);
        })
       
    },
  },
};
</script>

