import Vue from 'vue'
import HomeView from '@/views/IndexView/HomeView.vue'
import VueRouter from 'vue-router'
import ApplianceView from '@/views/IndexView/ApplianceView.vue'
import SortView from '@/views/IndexView/SortView.vue'
import RoundView from '@/views/IndexView/RoundView.vue'
import CarView from '@/views/IndexView/CarView.vue'
import MyView from '@/views/IndexView/MyView.vue'



import SearchView from '@/views/SearchView/SearchView.vue'
import SearchListView from '@/views/SearchView/SearchListView.vue'

import LoginView from '@/views/MyView/LoginView.vue'
import SetView from '@/views/MyView/SetView.vue'


import store from "@/store";
import ShopDetail from '@/views/ShopDetail.vue'
import PayView from '@/views/PayView.vue'
import RoundDetail from '@/views/RoundDetail.vue'
import GoodsPay from '@/views/GoodsPay.vue'
import PayDetail from '@/views/PayDetail.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: { showNav: true },

  },
  {
    path: '/detail',
    name: 'detail',
    component: ShopDetail
  },
  {
    path: '/goodspay',
    name: 'goodspay',
    component: GoodsPay
  },
   {
    path: '/paydetail',
    name: 'paydetail',
    component: PayDetail
  },

  {
    path: '/rounddatail',
    name: 'rounddetail',
    component: RoundDetail
  },

  {
    path: '/appliance',
    name: 'appliance',
    component: ApplianceView
  },

  {
    path: '/pay',
    name: 'pay',
    component: PayView
  },

  {
    path: '/sort',
    name: 'sort',
    component: SortView,
    meta: { showNav: true },

  },

  {
    path: '/round',
    name: 'round',
    component: RoundView,
    meta: { showNav: true },

  },

  {
    path: '/car',
    name: 'car',
    component: CarView,
    meta: {
      needLogin: true,
    },
  },

  {
    path: '/my',
    name: 'my',
    component: MyView,
    meta: { showNav: true },

  },
  {
    path: '/search',
    name: 'search',
    component: SearchView
  },
  {
    path: '/search/list',
    name: 'searchlist',
    component: SearchListView
  },

  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/my/set',
    name: 'set',
    component: SetView
  }

]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  let isLogin = store.state.token;
  // console.log("当前页面是否需要登录", to.meta.needLogin, "登录状态", isLogin);
  if (to.meta.needLogin && !isLogin) {
    // console.log("需要登录", "目标页面是", to.fullPath);
    next({
      path: "/login",
      query: {
        target: to.fullPath,
      },
    });
  } else {
    next();
  }
});
export default router
