<template>
  <!-- recommendation 推荐 -->
  <div class="recommendation">
    <van-divider>推荐商品</van-divider>
    <ul>
      <li v-for="(item, index) in recom_list" :key="index" @click="ToDetailPage(item.action.path)">
        <van-image class="img"
        width="160rem" height="160rem"
        lazy-load :src="item.image_url"
        @error="item.image_url = 'default-image.png'"  />
        <div class="info">
          <p>{{ item.name }}</p>
          <p>
            <span>￥</span>{{ item.price
            }}<span v-if="item.is_multi_price" class="delovo">
              <span>￥</span>{{ item.market_price }}</span
            >
          </p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import {
  // getxmSearchList,
  getxmSearchrecommendBlank,
} from "@/api/index";
export default {
  computed: {},

  data() {
    return {
      recom_list: [],
      // a:[
      //   {
      //     prop_cfg_id:8,
      //     prop_value_id:3615,
      //   },
      //   {
      //     prop_cfg_id:2,
      //     prop_value_id:3,
      //   }
      // ],
    };
  },
  methods: {
    ToDetailPage(id){
    this.$router.push({ name: 'detail', params: { id:id } });
  }

  },
  created() {
    getxmSearchrecommendBlank().then((response) => {
      this.recom_list = response.data.data.recom_list;
      console.log("recom_list", this.recom_list);
      
    });
  },
  watch: {},
};
</script>

<style lang="less" scoped>
.recommendation {
  .van-divider {
    font-size: 20rem;
    color: black;
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    padding: 0;
    list-style: none;

    li {
      flex: 1 1 calc(50% - 10px);
      box-sizing: border-box;
      border: 1px solid #ddd;
      padding: 10px;
      text-align: center;
      border-radius: 8px;
      background-color: #f9f9f9;

      .img {
        max-width: 100%;
        height: auto;
        border-radius: 4px;
      }

      .info {
        margin-top: 10px;

        p {
          margin: 5px 0;
          font-size: 14px;
          color: #333;
          text-align: left;
          &:first-child {
            width: 160px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
          &:nth-child(2) {
            font-size: 16px;
            font-weight: bold;
            color: #e63946;
            span {
              font-size: 12px;
            }
            .delovo {
              font-size: 14px;
              color: #888;
              text-decoration: line-through;
              span {
                font-size: 12px;
              }
            }
          }
        }
      }
    }
  }
}
</style>