<template>
  <div class="login-container">
    <h1>{{ hasAccount ? "茄子账号登陆" : "茄子账号注册" }}</h1>
    <div class="logo"><img src="@/assets/茄子-01.svg" alt="" /></div>
    <h2>让每个人都能享受科技的乐趣</h2>
    <div class="input-group">
      <input type="text" placeholder="Username" v-model="username" />
    </div>
    <div class="input-group">
      <input
        :type="hasAccount ? 'password' : 'text'"
        :placeholder="hasAccount ? 'Password' : 'Set a Password'"
        v-model="password"
      />
    </div>
    <h3>
      <input
        type="radio"
        name="must"
        value="on_or_off"
        v-model="isAgreementChecked"
      />已阅读并同意茄子账号<a>用户协议</a>和<a>隐私政策</a>
    </h3>
    <div class="button-group">
      <button
        class="btn btn-primary"
        @click="hasAccount ? loginHandler() : zhuceHandler()"
      >
        {{ hasAccount ? "登录" : "注册" }}
      </button>
    </div>
    <p class="toggle-text">
      {{ hasAccount ? "没有账号？" : "已有账号？" }}
      <a @click="toggleAccount">{{ hasAccount ? "立即注册" : "去登录" }}</a>
    </p>
    <van-loading
      class="ovoloading"
      size="30vw"
      type="spinner"
      v-if="ovoloading"
    />
  </div>
</template>

<script>
import { mapMutations, mapActions } from "vuex";
import { Notify } from "vant"; // 确保你引入了 Vant 的 Notify

export default {
  data() {
    return {
      hasAccount: true,
      username: "admin",
      password: "123456",
      isAgreementChecked: false,
      ovoloading: false,
    };
  },
  methods: {
    ...mapMutations(["setToken"]),
    ...mapActions(["addUser"]),

    loginHandler() {
      if (!this.isAgreementChecked) {
        Notify("请先勾选协议！");
        return;
      }

      this.ovoloading = true;
      const user = this.$store.state.userlist.find(
        (item) => item.username === this.username
      );

      if (user) {
        if (user.password === this.password) {
          // 登录成功，设置 token
          this.setToken(user.username);

          // 获取目标页面的路径，如果没有传递，则跳转到默认页面（如首页）
          const target = this.$route.query.target || "/";

          // 跳转到目标页面
          this.$router.replace(target).catch((err) => {
            // 捕获可能的导航错误（如目标页面相同）
            if (err.name !== "NavigationDuplicated") {
              console.error("导航错误：", err);
            }
          });
        } else {
          Notify("密码错误");
        }
      } else {
        Notify("用户不存在，跳转到注册");
        this.toggleAccount(); // 假设这是切换到注册页面的逻辑
      }

      this.ovoloading = false;
    },
    zhuceHandler() {
      const existingUser = this.$store.state.userlist.find(
        (user) => user.username === this.username
      );

      if (existingUser) {
        Notify("用户已存在");
        return;
      }

      const newUser = {
        username: this.username,
        password: this.password,
      };

      this.addUser(newUser); // 使用 Vuex 的 addUser 方法

      this.username = "";
      this.password = "";
    },

    toggleAccount() {
      this.hasAccount = !this.hasAccount;
    },
  },
};
</script>
