<template>
  <div class="all">
    <van-overlay :show="show" z-index="10000">
      <van-loading type="spinner"  class="loding" /></van-overlay>
    <van-sidebar v-model="activeKey" @change="onChange()" class="sort">
      <template v-for="(item, index) in category_name">
        <van-sidebar-item
          :title="item.category_name"
          :key="index"
          @click="getthisid(item.category_id)"
        />
      </template>
    </van-sidebar>
    <div class="content"><SortCom :contid="contid"/></div>
  </div>
</template>

<script>
import axios from "axios";
import SortCom from "@/components/sort/SortCom.vue";
export default {
  components: {
    SortCom,
  },
  data() {
    return {
      activeKey: 0,
      all: [],
      contid: 1242,
      category_name: null,
      show: true,
    };
  },
  methods: {
    getthisid(id) {
      this.contid = id;
      console.log(this.contid);

    },
    onChange() {
      console.log("被点击");
    },
    getdata() {
      axios
        .get(`https://apis.netstart.cn/xmsc/home/category_v2`)
        .then((response) => {
          console.log("data:", response.data.data);
          this.category_name = response.data.data;
        })
        .catch((error) => {
          console.error("获取data失败:", error);
        });
    },
  },
  created() {
    this.getdata();
    setTimeout(() => {
      this.show = false;
    }, 3000);
  },

};
</script>

<style lang="less" scoped>
.all {
  .loding {
    top: 45%;
    left: 45%;
  }
  margin-bottom: 60rem;
  .sort {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    overflow-y: auto;
    z-index: 10;
    overflow-y: scroll; /* 确保有滚动功能 */
    scrollbar-width: none; /* Firefox 隐藏滚动条 */
    -ms-overflow-style: none; /* IE/Edge 隐藏滚动条 */
  }
  .content {
    margin-left: 80px; /* 留出侧边栏的宽度 */
    height: calc(100vh - 60rem);
    flex: 1; /* 内容区域占满剩余空间 */
  }
}
</style>