<template>
  <div>
    <template v-for="(item, index) in data.category_list">
      <template v-if="item.view_type === 'cells_auto_fill'">
        <div :key="'img' + index" class="sortimg">
          <img :src="item.body.items[0].img_url" alt="" />
        </div>
      </template>
    </template>
    <div class="title_list">
      <template v-for="(item, index) in data.title_list">
        <a href="" :key="index"> {{ item.title }}</a>
      </template>
    </div>
    <div class="content">
      <template v-for="(item, index) in data.category_list">
        <template v-if="item.view_type === 'category_title'">
          <div :key="'asdasd' + index" class="sorttitle">
            {{ item.body.category_name }}
          </div>
        </template>
        <template v-if="item.view_type === 'category_group'">
          <div :key="index" class="item_list">
            <!-- items -->
            <template v-if="item.body.items">
              <div
                v-for="item1 in item.body.items"
                :key="item1.action.path"
                class="sortitem"  @click="jumpsearch(item1.product_name)"
              >
                <van-image  lazy-load class="img" :src="item1.img_url" alt="" />
                <span>{{ item1.product_name }}</span>
              </div>
            </template>
            <!-- list -->
            <template v-if="item.body.product_list">
              <div :key="index" class="item_list">
                <div
                  v-for="item1 in item.body.product_list"
                  :key="item1.product_id"
                  class="sortlist"  @click="jump(item1.product_id)"
                >
                  <van-image  lazy-load class="img" :src="item1.puzzle_url" alt="" />
                  <div class="info">
                    <div>{{ item1.name }}</div>
                    <div>
                     <span class="price">{{ item1.price }}</span> 
                      <template v-for="(a, b) in item1.labels">
                        <span :key="b"
                          ><span class="label">{{ a }}</span></span
                        >
                      </template>
                    </div>
                  </div>
                </div>
              </div>
            </template>
          </div>
        </template>
      </template>
    </div>
  </div>
</template>




<script>
import axios from "axios";

export default {
  props: { contid: Number },
  data: function () {
    return {
      data: [],
    };
  },
  methods: {
    jump(id){
      this.$router.push({ name: "detail", query: { id: id } });

    },
    jumpsearch(name){
      
        this.$router.push({ path: `/search/list?key=${name}` });
    },
    getdata(id) {
      axios
        .get(`https://apis.netstart.cn/xmsc/home/category_v2`, {
          params: {
            cat_id: id,
          },
        })
        .then((response) => {
          console.log("data:", response.data.data[0]);
          this.data = response.data.data[0];
        })
        .catch((error) => {
          console.error("获取data失败:", error);
        });
    },
  },
  created() {
    this.getdata(this.contid);
  },
  watch: {
    // 监听父组件传递的 contid 变化
    contid(newContid) {
      this.getdata(newContid); // 重新调用 getdata 方法
    },
  },
};
</script>

<style lang="less" scoped>
.sortimg {
  line-height: 0;
  img {
    width: 100%;
  }
}
.title_list {
  display: flex;
  padding: 5px;
  border-bottom: 1px solid #999;
  background-color: white;
  overflow-x: auto; /* 允许横向滚动 */
  scrollbar-width: none; /* Firefox: 隐藏滚动条 */
  -ms-overflow-style: none; /* IE 和 Edge: 隐藏滚动条 */
  a {
    margin-right: 10px;
    padding: 5px;
    white-space: nowrap;
    border-radius: 10px;
    font-size: 16px;
    font-weight: 300;
    &:active {
      background: rgba(205, 199, 199, 0.631);
    }
  }
}
.content {
  padding: 20px 10px;
  padding-bottom: 60rem;
  .sorttitle {
    margin-bottom: 20px;
  }
  .item_list {
    display: flex;
    flex-wrap: wrap;
    .sortlist {
      display: flex;
      background-color: #f8f8f8;
      width: 100%;
      padding: 10px 0;
      margin: 10px 0;
      border-radius: 10px;
      .info {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        > div {
          &:first-child {
            font-size: 18px;
            font-weight: 500;
          }
          .price {
            
            font-size: 16px;
            &::before {
              content: "￥";
              font-size: 14px;
            }
            &::after {
              content: "起";
              font-size: 14px;
            }
          }
          .label{
            font-size: 12px;
            padding: 0 5px;
            color: #cda87e;
            border: 1px solid #cda87e;
            margin-left: 10px;
          }
        }
      }
      .img {
        width: 90px;
        height: auto;
      }
    }
    .sortitem {
      width: 90px;
      display: flex;
      flex-direction: column;
      align-items: center;
      .img {
        width: 55px;
      }
      span {
        font-size: 14px;
        margin: 20px;
        text-align: center;
      }
    }
  }
}
</style>