<template>
  <div>
    <div class="returrn">
      <div class="son">
        <van-icon name="arrow-left" @click="$router.go(-1)" class="edit" />
      </div>
    </div>
    <div class="address-title">
        <div class="title">收货地址</div>
    </div>
    <div class="resslist">
      <div class="ress" v-for="(item, index) in ADDRESSList" :key="index">
        <div class="name">
          {{ item.name }} &nbsp; {{ item.tel }} &nbsp;<span
            class="Moreno"
            v-if="item.isDefault"
            >默认</span
          >
        </div>
        <div class="ressadd">{{ item.address }}</div>
        <van-icon name="edit" class="edit" />
      </div>
    </div>

    <van-cell is-link @click="showPopup">新增地址</van-cell>
    <van-popup v-model="show" position="bottom" :style="{ height: '70%' }">
      <van-address-edit
        ref="AddressEdit"
        :area-list="areaList"
        show-postal
        show-delete
        show-set-default
        show-search-result
        :search-result="searchResult"
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        @save="onSave"
        @delete="onDelete"
        @change-detail="onChangeDetail"
      />
    </van-popup>

    <div class="out" @click="outuser">退出登录</div>
  </div>
</template>

<script>
import { Toast } from "vant";
import { areaList } from "@vant/area-data";

import { mapState, mapMutations } from "vuex";

export default {
  computed: {
    ...mapState(["ADDRESSList"]),
  },
  data() {
    return {
      chosenAddressId: "1",
      show: false,
      areaList,
      searchResult: [],
    };
  },
  methods: {
    ...mapMutations(["setADDRESSList", "LOGINOUT"]),
    outuser() {
      this.LOGINOUT(); // 调用 Vuex 的 mutation
      this.$router.go(-1); // 返回上一页
    },
    showPopup() {
      this.show = true;
    },
    onAdd() {},
    onEdit(item, index) {
      Toast("编辑地址:" + index);
    },
    onSave() {
      Toast("save");
      const newItem = {
        id: (this.$store.state.ADDRESSList.length + 1).toString(), // 生成新的 id
        name: this.$refs.AddressEdit.data.name,
        tel: this.$refs.AddressEdit.data.tel,
        address:
          this.$refs.AddressEdit.data.province +
          this.$refs.AddressEdit.data.city +
          this.$refs.AddressEdit.data.county +
          this.$refs.AddressEdit.data.country,
        isDefault: this.$refs.AddressEdit.data.isDefault, // 默认为非默认地址
      };

      // 调用 Vuex 中的 addAddress mutation
      this.$store.commit("addAddress", newItem);
    },
    onDelete() {
      Toast("delete");
    },
    onChangeDetail(val) {
      if (val) {
        this.searchResult = [
          {
            name: "黄龙万科中心",
            address: "杭州市西湖区",
          },
        ];
      } else {
        this.searchResult = [];
      }
    },
  },
};
</script>

<style lang="less" scoped>
.address-title{
  padding: 10px;
  border-bottom: 1px solid #9999993c;
}
.out {
  padding: 20px;
  text-align: center;
}
.resslist {
  background-color: #f5f5f5;
  padding: 10px;
  .ress {
    padding: 20px;
    padding-right: 40px;
    margin-bottom: 10px;
    background-color: #ffffff;
    border-radius: 20px;
    position: relative;
    .Moreno {
      background-color: red;
      color: white;
      border-radius: 20px;
      font-size: 14px;
      padding: 2px;
    }
    .name {
      margin-bottom: 10px;
      box-sizing: border-box;
      overflow: hidden;
    }
    .ressadd {
      font-size: 14px;
      font-weight: 600;
    }
    .edit {
      position: absolute;
      right: 32rem;
      top: 24rem;
      font-size: 30px;
    }
  }
}

</style>