<template>
  <div>
    <ul class="classes">
      <li
        v-for="(item, o) in classes"
        :key="o"
        @click="$store.dispatch('ovoSearch', item.class_name)"
      >
        <img :src="item.class_icon" alt="" />
        <span>{{ item.class_name }}</span>
      </li>
    </ul>
    <ul class="searchproduct">
      <li
        v-for="(item, index) in searchproduct"
        :key="index"
        @click="ToDetailPage(item.body.product_id)"
      >
        <img v-lazy="item.body.image" />
        <div class="shop-info">
          <div class="shop-name">{{ item.body.name }}</div>
          <div class="shop-desc">{{ item.body.desc }}</div>
          <div class="shop-parameters">
            <span v-for="(op, a) in item.body.class_parameters?.list" :key="a">
              <div>
                {{ op.top_title }}
              </div>
              <div>{{ op.value }}</div>
            </span>
          </div>
          <div class="shop-price">
            <span>
              <span class="price-icon">¥</span>
              {{ item.body.price }}
              <span v-if="item.body.is_virtual_product" class="qi">起</span>
            </span>
            <span>
              <span v-if="item.body.is_virtual_product" class="del">
                <span class="price-icon">¥</span>
                {{ item.body.market_price }}
              </span>
            </span>
          </div>
          <div class="shop-comments">
            {{ item.body.comments_total }}条评论 &nbsp;&nbsp;{{
              item.body.satisfy_per
            }}满意
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["classes", "searchproduct"]), // 映射 Vuex state 的 classes 到组件中
  },
  methods: {
    ToDetailPage(id) {
      this.$router.push({ name: "detail", query: { id: id } });
    },
  },
};
</script>

<style lang="less" scoped>
.classes {
  display: flex;
  gap: 10px;
  padding: 10px;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  background-color: #f4f4f4;
  padding: 10px;

  &::-webkit-scrollbar {
    height: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background: #aaa;
    border-radius: 4px;

    &:hover {
      background: #666;
    }
  }
  -ms-overflow-style: none; /* 隐藏 IE 和 Edge 的滚动条 */
  scrollbar-width: none; /* 隐藏 Firefox 的滚动条 */

  li {
    list-style: none;
    flex-shrink: 0;
    text-align: center;
    background-color: white;

    img {
      width: 70px;
      display: block;
      margin: 0 auto 5px;
      object-fit: cover;
    }

    span {
      font-size: 14px;
      color: #333;
    }

    &:hover {
      transform: scale(1.05);
      transition: transform 0.2s ease;
    }
  }
}

.searchproduct {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 20px;

  li {
    display: flex;
    padding: 15px;
    border: 1px solid #e5e5e5;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.02);
    }

    img {
      width: 120px;
      height: 120px;
      object-fit: cover;
      border-radius: 10px;
      margin-right: 15px;
    }

    .shop-info {
      display: flex;
      flex-direction: column;
      flex: 1;

      .shop-name {
        font-size: 16px;
        font-weight: bold;
        color: #333;
        margin-bottom: 8px;
      }

      .shop-desc {
        font-size: 14px;
        color: #666;
        line-height: 1.4;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 10px;
      }

      .shop-parameters {
        display: flex;
        justify-content: space-around;

        margin-bottom: 10px;
        span {
          flex-wrap: wrap;
          display: flex;
          flex-direction: column;
          font-size: 12px;
          color: #888;

          div {
            margin: 2px 0;
            text-align: center;
            width: 50px;
            white-space: nowrap;
            overflow: hidden;
          }
        }
      }

      .shop-price {
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin-bottom: 10px;

        span {
          font-size: 16px;
          color: #f40;
          font-weight: bold;
        }

        .del {
          font-size: 14px;
          color: #aaa;
          text-decoration: line-through;
        }

        .price-icon {
          font-size: 16px;
        }

        .qi {
          font-size: 12px;
          color: #f40;
        }
      }

      .shop-comments {
        font-size: 12px;
        color: #888;
      }
    }
  }
}
</style>