<template>
  <div>
    <van-swipe class="img-swipe" @change="onChange">
      <van-swipe-item
        v-for="(item, index) in all.goods_share_datas.gallery_view"
        :key="index"
        ><van-image :src="item.img_url" alt="">
          <template v-slot:error>加载失败</template>
        </van-image></van-swipe-item
      >
      <template #indicator>
        <div class="custom-indicator">
          {{ current + 1 }}/{{ all.goods_share_datas.gallery_view?.length }}
        </div>
      </template>
    </van-swipe>
  </div>
</template>

<script>
export default {
  props: {
    all: Object,
  },
  data() {
    return {
      current: 0,
    };
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
  },
};
</script>

<style lang="less" scoped>
.img-swipe {
  line-height: 0;

  img {
    width: 100%;
  }
  .custom-indicator {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 2px 5px;
  font-size: 12px;
  margin: 20px;
}

}
</style>