<template>
  <div style="position: relative; height: 100vh">
    <div class="returrn" style="background-color: #ff4100">
      <div class="son">
        <van-icon name="arrow-left" @click="$router.go(-1)" class="edit" />
      </div>
    </div>
    <header>
      <i><van-icon name="cash-back-record-o" /></i>
      <b>
        <h1><span>待付款</span></h1>
        <h2>你的订单已确定，请在规定时间内完成支付，过时订单将自动关闭</h2>
        <!-- <h3><span> 支付剩余时间 </span><span>10s</span></h3> -->
      </b>
    </header>
    <main>
      <h1>
        <van-contact-card
          type="edit"
          name="张三"
          tel="13000000000"
          :editable="false"
        />
      </h1>
      <h2>
        <div class="order">
          <i class="title"><span>官方自营</span> <span>去付款</span></i>
          <van-divider />
          <template v-for="(item, index) in ovo.selectedItems">
            <div :key="index" class="item-boos">
              <div class="item">
                <span class="item-img">
                  <van-image class="img" :src="item.img" alt="">
                    <template v-slot:error>加载失败</template>
                  </van-image></span
                >
                <span class="info"
                  ><b class="name">{{ item.name }}({{ item.price }}RMB)</b>
                  <b class="quantity"
                    ><span class="price">{{ item.price * item.quantity }}</span
                    >数量:{{ item.quantity }}</b
                  ></span
                >
              </div>
            </div>
          </template>
          <div class="orderid">
            <div>
              <span>订单编号:</span> <span>{{ ovo.id }}</span>
            </div>
            <div>
              <span>支付金额:</span>
              <span style="font-weight: 800">{{ allprice }} RMB</span>
            </div>
          </div>
        </div>
      </h2>
    </main>
    <div class="pass" v-if="pageshow">
      <div class="returrn">
        <div class="son">
          <van-icon name="arrow-left" @click="pageshow = false" class="edit" />
        </div>
      </div>
      <div class="allprice">￥{{ allprice }}</div>
      <van-password-input
        :value="value"
        :focused="showKeyboard"
        @focus="show = true"
      />
      <!-- 数字键盘 -->
      <van-number-keyboard
        :show="show"
        @blur="show = false"
        @input="onInput"
        @delete="onDelete"
      />
    </div>
    <div class="pay">
      <button @click="payNow" class="pay-button">立即支付</button>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data() {
    return {
      ovo: [],
      allprice: 0,
      value: "",
      showKeyboard: false,
      pageshow: false,
      show: false,
    };
  },
  mounted() {
    if (this.$route.query.list) {
      try {
        this.ovo = JSON.parse(this.$route.query.list);
        console.log(this.ovo); // 输出传递的参数
        this.ovo.selectedItems.forEach((item) => {
          this.allprice += item.price * item.quantity;
          console.log("ovo", this.ovo);
        });
      } catch (e) {
        console.error("JSON 解析失败:", e);
      }
    } else {
      console.error("未接收到参数");
    }
  },
  created() {
    console.log("currentUser", this.currentUser);
    console.log("userlist", this.userlist);
  },
  computed: {
    // 获取 vuex 中的 userlist 数据
    userlist() {
      return this.$store.state.userlist;
    },
    // 获取本地存储中的 token
    token() {
      return localStorage.getItem("token");
    },
    // 获取 token 中的用户名

    // 从 token 获取 username
    username() {
      return this.token ? JSON.parse(this.token) : "";
    },
    currentUser() {
      return this.userlist.find((item) => item.username === this.username);
    },

    password() {
      // 在 userlist 中查找与当前用户名匹配的用户
      const user = this.userlist.find(
        (item) => item.username === this.username
      );
      return user ? user.password : ""; // 如果找到用户，则返回密码，否则返回空字符串
    },
  },
  methods: {
    updata() {
      // 获取本地存储中的 userlist 数据
      let userlist = JSON.parse(localStorage.getItem("userlist"));

      if (!userlist) {
        console.error("未找到用户列表");
        return;
      }

      // 找到当前用户
      const user = userlist.find(
        (item) => item.username === this.currentUser.username
      ); // 通过当前用户名查找用户
      if (!user) {
        console.error("未找到当前用户或订单列表");
        return;
      }

      // 查找订单并更新 ispay 属性
      const order = user.order.find((item) => item.id === this.ovo.id);
      if (order) {
        console.log("abc:", order);
        order.ispay = true; // 更新 ispay 为 true
        console.log("订单更新成功:", order);

        // 将更新后的 userlist 保存回本地存储
        localStorage.setItem("userlist", JSON.stringify(userlist));
      } else {
        console.error("未找到匹配的订单");
      }
    },
    payNow() {
      this.showKeyboard = true;
      this.pageshow = true;
      this.show = true;
    },
    onInput(num) {
      this.value += String(num);
    },
    onDelete() {
      this.value = this.value.slice(0, -1);
    },
  },
  watch: {
    value(value) {
      if (value.length === 6 && value === this.password) {
        Toast("支付成功");
        this.updata();
        this.$router.push({ path: "/pay" });
      } else if (value.length === 6 && value !== this.password) {
        Toast("密码错误");
      }
    },
  },
};
</script>

<style lang="less" scoped>
header {
  display: flex;
  padding: 10px 30px;
  background-color: #ff4100;
  border-radius: 0 0 12px 12px;
  color: white;
  i {
    font-size: 35px;
    margin-right: 10px;
  }
  b {
    h1 {
      font-size: 20px;
      font-weight: 600;
      margin: 10px 0;
    }
    h2 {
      font-size: 14px;
      margin-bottom: 20px;
    }
    h3 {
      margin-bottom: 20px;
    }
  }
}
main {
  padding: 10px;
  h1 {
    border: 1px solid #ccc;
    border-radius: 12px;
    .van-cell {
      border-radius: 12px;
    }
  }
  h2 {
    .order {
      padding: 20px 10px;
      box-sizing: border-box;
      background-color: white;
      border-radius: 12px;
      padding: 10px 0;
      margin: 10px 0;

      .title {
        margin: 0 10px;
        display: block;
        display: flex;
        justify-content: space-between;
      }
      .item-boos {
        margin: 10px 0;
        .item {
          display: flex;
          .item-img {
            display: flex;
            justify-content: center;
            align-items: center;
            .img {
              width: 100px;
            }
          }
          .info {
            margin-left: 20px;
            padding-top: 20px;
            padding-right: 10px;
            .name {
              font-size: 16px;
              font-weight: 600;
              line-height: 20px;
            }
            .quantity {
              margin-top: 30px;
              font-size: 14px;
              color: #999;
              display: flex;
              justify-content: space-between;
              align-items: center;
              .price {
                &::before {
                  content: "￥";
                }
                font-size: 18px;
                color: red;
              }
            }
          }
        }
      }
      .orderid {
        padding: 0 10px;
        margin: 10px 0;
        font-size: 14px;
        > div {
          margin: 10px 0;
          display: flex;
          justify-content: space-between;
        }
      }
    }
  }
}
.pay {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  .pay-button {
    background-color: #ff5722;
    width: 100%;
    color: white;
    font-size: 16px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .pay-button:hover {
    background-color: #e64a19;
  }
}
.pass {
  position: absolute;
  top: 0;
  height: 0;
  width: 100vw;
  height: 100vh;
  background-color: white;
  z-index: 1001;
  .allprice {
    font-size: 30px;
    font-weight: 800;
    text-align: center;
    margin: 60rem 0;
  }
}
</style>