<template>
  <div>
    <template v-for="item in questionA">
      <template v-if="item.content.id === 0">
        <div :key="item.content.announce_id" class="q-box">
          <div class="q-title">
            <span> {{ item.content.title }}</span>
            <!-- <van-icon name="ellipsis" /> -->
          </div>
          <div class="q-tag" v-for="i in item.content.tag_list" :key="i.id">
            <span>
              <van-image class="img" :src="i?.img" alt="" /><i>{{ i.tag }}</i>
            </span>
          </div>
          <div
            class="q-an"
            v-for="(a, b) in item.content.question.answer_list_v2"
            :key="b"
          >
            <img class="img" :src="a?.icon" alt="" />
            <i>{{ a.answer }}</i>
          </div>
          <div class="q-icon">
            <span> <van-icon name="share-o" /></span
            ><span
              ><van-icon name="chat-o" /><b>{{
                item.content.question?.answer_num
              }}</b></span
            ><span
              ><van-icon name="good-job-o" /><b>{{
                item.content.question?.praise_num
              }}</b></span
            >
          </div>
        </div>
      </template>
      <template v-if="item.content.id !== 0">
        <div :key="item.content.announce_id" class="r-box">
          <div class="r-top">
            <span><img :src="item.content.icon" alt="" /></span
            ><b>{{ item.content.nickname }}</b>
          </div>
          <!-- title -->
          <div class="r-title">
            <template v-if="item.content.title">
              {{ item.content?.title }}</template
            >
            <template v-else>
              <span class="r-title-content">{{
                item.content?.content
              }}</span></template
            >
          </div>
          <!-- imglist -->
          <div class="r-img">
            <template v-for="(rimg, r) in item.content.pic_list.slice(0, 3)">
              <img :src="rimg?.img" :key="r" alt="" />
            </template>
          </div>
          <!-- tag -->
          <div class="q-tag">
            <span v-for="i in item.content.tag_list" :key="i.id">
              <van-image class="img" :src="i?.img" alt="" /><i>{{ i.tag }}</i>
            </span>
          </div>
          <!-- hot_comment -->
          <template v-if="item.content.hot_comment">
            <div class="r-hot">
              <span>{{ item.content.hot_comment.name }}</span>
              <b>{{ item.content.hot_comment.comment }}</b>
            </div>
          </template>
          <!-- video_list -->
          <template v-if="item.content.video_list">
            <div class="r-video">
              <template v-for="(v, h) in item.content.video_list.slice(0, 3)">
                <img :src="v.video_url" :key="'aaa' + h" alt="" />
              </template>
            </div>
          </template>

          <!-- icon -->
          <div class="q-icon">
            <span> <van-icon name="share-o" /></span
            ><span
              ><van-icon name="chat-o" /><b>{{
                item.content.comment_num
              }}</b></span
            ><span
              ><van-icon name="good-job-o" /><b>{{
                item.content.praise_num
              }}</b></span
            >
          </div>
        </div>
      </template>
    </template>
    <template>
      <div></div>
    </template>
  </div>
</template>


<script>
import axios from "axios";
export default {
  data() {
    return {
      questionA: [],
    };
  },
  methods: {
    getcontent() {
      axios
        .get(`https://apis.netstart.cn/xmsc/communicate/home_rec?size=10`)
        .then((response) => {
          this.questionA = response.data.data.rec_list;
        })
        .catch((error) => {
          console.error("获取评论失败:", error);
        });
    },
  },
  created() {
    this.getcontent();
  },
};
</script>

<style lang="less" scoped>
.q-box {
  padding: 10px;
  background-color: white;
  margin-top: 10px;
  .q-title {
    display: flex;
    align-items: center;
    margin: 10px 0;
    &::before {
      content: "问";
      color: red;
      padding: 5px;
      border-radius: 50%;
      margin-right: 6px;
      font-size: 11px;
      background-color: rgba(232, 204, 204, 0.268);
    }
  }
  .q-tag {
    margin: 10px;
    span {
      display: flex;
      align-items: center;
      justify-content: left;
      margin-left: 10px;
      .img {
        width: 15px;
        height: 15px;
        border-radius: 50%;
        margin-right: 5px;
      }
      i {
        color: #999;
        font-size: 12px;
      }
    }

    padding-bottom: 20px;
    border-bottom: 1px solid #9999996f;
  }
  .q-an {
    display: flex;
    align-items: center;
    margin: 10px 0;
    .img {
      text-align: center;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      margin-right: 5px;
    }

    i {
      width: 80%;
      font-size: 15px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  .q-icon {
    display: flex;
    justify-content: space-around;
    font-size: 25px;
    color: #999;
    margin: 9px 0;
    padding: 9px 0;
    span {
      display: flex;
      align-items: center;
      b {
        font-size: 16px;
        margin-left: 5px;
        font-weight: 750;
      }
    }
  }
}
.r-box {
  padding: 10px;
  background-color: white;
  margin-top: 10px;
  .r-top {
    display: flex;
    align-items: center;
    font-size: 14px;
    img {
      width: 30px;
      border-radius: 50%;
      margin-right: 10px;
    }
  }
  .r-title {
    margin: 20px 0;
    .r-title-content {
      display: -webkit-box; /* 使用 Flexbox 的 Webkit 模型 */
      -webkit-box-orient: vertical; /* 设置盒子方向为垂直 */
      -webkit-line-clamp: 3; /* 限制显示的行数 */
      overflow: hidden; /* 隐藏超出内容 */
      text-overflow: ellipsis; /* 超出部分显示省略号 */
      word-break: break-word; /* 处理长单词的换行 */
    }
  }
  .r-hot {
    display: flex;
    background-color: #f6f6f6;
    padding: 8px;
    font-size: 14px;
    span {
      color: #999;
      overflow: hidden;
      white-space: nowrap;
      &::after {
        content: "：";
      }
    }
    b {
      width: 80%;
      overflow: hidden;
      white-space: nowrap;
    }
  }
  .r-img {
    display: flex;
    justify-content: left;
    img {
      width: 115px;
      height: 115px;
      object-fit: cover; /* 确保图片按比例缩放 */
      margin-right: 10px;
    }
  }
  .q-tag {
    display: flex;
    margin: 10px;
    align-items: center;
    span {
      display: flex;
      align-items: center;
      justify-content: left;
      margin-right: 10px;
      .img {
        width: 15px;
        height: 15px;
        border-radius: 50%;
        margin-right: 5px;
      }
      i {
        color: #999;
        font-size: 12px;
      }
    }
  }
  .q-icon {
    display: flex;
    justify-content: space-around;
    font-size: 25px;
    color: #999;
    margin: 9px 0;
    padding: 9px 0;
    span {
      display: flex;
      align-items: center;
      b {
        font-size: 16px;
        margin-left: 5px;
        font-weight: 750;
      }
    }
  }
}
</style>