import Vue from 'vue';
import Vuex from 'vuex';
import axios from "axios";

Vue.use(Vuex);

function safeJSONParse(data, fallback = []) {
  try {
    return data ? JSON.parse(data) : fallback; // 如果数据为null或undefined，返回默认值
  } catch (e) {
    console.error("Error parsing JSON:", e);
    return fallback; // 如果解析失败，返回默认值
  }
}

export default new Vuex.Store({
  state: {
    historyList: safeJSONParse(localStorage.getItem("historyList")),
    classes: safeJSONParse(localStorage.getItem("classes")),
    searchproduct: safeJSONParse(localStorage.getItem("searchproduct")),
    token: safeJSONParse(localStorage.getItem("token")),
    userlist: (function () {
      const storedUserlist = safeJSONParse(localStorage.getItem("userlist")) || [
        {
          username: "admin",
          password: "123456",
          carsID: [
          ],
        },
        {
          username: "nick",
          password: "123",
          carsID: [],
        },
      ];
      return storedUserlist.map((user) => ({
        ...user,
        carsID: user.carsID || [], // 确保每个用户的 carsID 属性是数组
      }));
    })(),
    ADDRESSList: safeJSONParse(localStorage.getItem("ADDRESSList")) || [
      {
        id: "1",
        name: "张三",
        tel: "13000000000",
        address: "浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室",
        isDefault: true,
      },
      {
        id: "2",
        name: "李四",
        tel: "13100000000",
        address: "浙江省杭州市拱墅区莫干山路 50 号",
      },
    ],
  },
  getters: {},
  mutations: {
    REMOVE_ORDER(state, { username, orderId }) {
      const user = state.userlist.find((user) => user.username === username);
      if (user && Array.isArray(user.order)) {
        const orderIndex = user.order.findIndex((order) => order.id === orderId);
        if (orderIndex !== -1) {
          user.order.splice(orderIndex, 1);
          localStorage.setItem("userlist", JSON.stringify(state.userlist));
        } else {
          console.log("订单 ID 无效");
        }
      } else {
        console.log("用户不存在或订单列表无效");
      }
    }
    ,
    UPDATE_SELECTED_ITEMS(state, updatedItemsList) {
      // 获取当前用户
      const user = state.userlist.find(user => user.username === state.username);

      if (user) {
        // 更新该用户的 selectedItemsList
        user.selectedItemsList = updatedItemsList;
      }
    },    // 添加新用户
    ADD_USER(state, newUser) {
      const userWithDefaultCarsID = {
        ...newUser,
        carsID: newUser.carsID || [], // 初始化 carsID 属性
      };
      state.userlist.push(userWithDefaultCarsID);
      localStorage.setItem("userlist", JSON.stringify(state.userlist));
    },
    REMOVE_PRODUCT(state, { username, index }) {
      const user = state.userlist.find((user) => user.username === username);
      if (user) {
        // 根据传递的索引删除商品
        if (index !== -1) {
          // 如果索引有效，移除商品
          user.carsID.splice(index, 1);

          // 保存更新后的 userlist 到 localStorage
          localStorage.setItem("userlist", JSON.stringify(state.userlist));
        } else {
          console.log("商品索引无效");
        }
      } else {
        console.log("用户不存在");
      }
    },


    // 添加商品到用户的购物车
    ADD_PRODUCT(state, { username, newProduct }) {
      const user = state.userlist.find((user) => user.username === username);
      if (user) {
        // 查找是否已存在相同 name 的商品
        const existingProduct = user.carsID.find((product) => product.name === newProduct.name);

        if (existingProduct) {
          // 如果商品已经存在，增加 quantity
          existingProduct.quantity += 1;
        } else {
          // 如果商品不存在，添加新商品
          user.carsID.push(newProduct);
        }

        // 保存更新后的 userlist 到 localStorage
        localStorage.setItem("userlist", JSON.stringify(state.userlist));
      }
    },

    // 更新用户的购物车
    UPDATE_USER_CARS(state, { username, newCarsID }) {
      const user = state.userlist.find(user => user.username === username);
      if (user) {
        user.carsID = newCarsID; // 更新购物车数据
        localStorage.setItem("userlist", JSON.stringify(state.userlist)); // 同步到 localStorage
      }
    },

    // 地址管理相关操作
    setADDRESSList(state, newList) {
      state.ADDRESSList = newList;
      localStorage.setItem("ADDRESSList", JSON.stringify(newList));
    },
    addAddress(state, newAddress) {
      state.ADDRESSList.push(newAddress);
      localStorage.setItem("ADDRESSList", JSON.stringify(state.ADDRESSList));
    },
    removeAddress(state, id) {
      state.ADDRESSList = state.ADDRESSList.filter((item) => item.id !== id);
      localStorage.setItem("ADDRESSList", JSON.stringify(state.ADDRESSList));
    },

    // 清空历史记录
    CLEAR_HISTORY_LIST(state) {
      state.historyList = [];
      localStorage.setItem("historyList", JSON.stringify([]));
    },

    // 更新历史记录
    UPDATE_HISTORY_LIST(state, newList) {
      state.historyList = newList;
      localStorage.setItem("historyList", JSON.stringify(state.historyList));
    },

    // 添加搜索记录
    ADD_TO_HISTORY(state, searchText) {
      if (searchText) {
        const index = state.historyList.indexOf(searchText);
        if (index !== -1) {
          state.historyList.splice(index, 1);
        }
        state.historyList.unshift(searchText);
        localStorage.setItem("historyList", JSON.stringify(state.historyList));
      }
    },

    // 设置本地存储的 classes 数据
    SET_CLASSES(state, classes) {
      state.classes = classes;
      localStorage.setItem("classes", JSON.stringify(classes));
    },

    // 设置本地存储的 searchproduct 数据
    SET_SEARCHPRODUCT(state, searchproduct) {
      state.searchproduct = searchproduct;
      localStorage.setItem("searchproduct", JSON.stringify(searchproduct));
    },

    // 合并更新多个本地存储数据的 mutation
    SET_STORAGE(state, { classes, searchproduct }) {
      if (classes !== undefined) {
        state.classes = classes;
        localStorage.setItem("classes", JSON.stringify(classes));
      }
      if (searchproduct !== undefined) {
        state.searchproduct = searchproduct;
        localStorage.setItem("searchproduct", JSON.stringify(searchproduct));
      }
    },

    setToken(state, payload) {
      state.token = payload;
      localStorage.setItem("token", JSON.stringify(payload));
    },

    LOGINOUT(state) {
      state.token = null;
      console.log("退出登录成功");
      localStorage.setItem("token", JSON.stringify(null));
    },
  },
  actions: {
    removeOrder({ commit }, payload) {
      commit('REMOVE_ORDER', payload);
    }, addUser({ commit }, newUser) {
      commit("ADD_USER", newUser);
    },
    addProduct({ commit }, payload) {
      commit("ADD_PRODUCT", payload);
    },
    updateUserCars({ commit }, { username, newCarsID }) {
      commit("UPDATE_USER_CARS", { username, newCarsID });
    },
    ovoSearch({ commit }, ovo) {
      axios.get(`https://apis.netstart.cn/xmsc/hisearch/query_v3`, {
        params: {
          query: ovo,
        },
      }).then((response) => {
        commit("SET_STORAGE", {
          classes: response.data.data.classes,
          searchproduct: response.data.data.list_v2,
        });
      }).catch((error) => {
        console.error("Error during ovo search: ", error);
      });
    }
  },
  modules: {},
});
